.header.center.orange-text {
  text-transform: uppercase;
  font-weight: 900;
}

body {
  background: url("../images/bulls_and_cows.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

}

.light {
  font-weight: 800;
  color: #00acdc;
  letter-spacing: 1px;
}

#gray {
  display: none;
  position: fixed;
  background-color: rgba(1, 1, 1, 0.75);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  z-index: 100;
}

#modal-form {
  padding: 20px;
  display: none;
  width: 80vw;
  height: 100%;
  background-color:#00acdc;
  font-size: 20px;
  text-align: center;
  border-radius: 10px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  z-index: 150;
  font-weight: 600;
  opacity: 0.8;
}

.preloader-wrapper.big {
  width: 100px;
  height: 100px;
  margin: 0 auto;
}

.preloader-wrapper {
  left: calc(50vw - 50px);
  top: 50vh;
}

#input {
  text-align: center;
  letter-spacing: .5px;
  border: 1px solid #26a69a;
  //box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25) inset, 0 2px 2px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  background: white;
  outline: none;
  color: #000000;
  width: 10%;
  height: 36px;
}

.header__modal-form {
  display: flex;
  justify-content: center;
}

.result__container tr th, td {
  text-align: center;
}

.result__container {
  background-color:#00acdc;
}

#exit {
  background-color: red;
}
